import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations } = createNamespacedHelpers('Chat')

export default {
	name: 'RoomSelect',
	props: {
		isVisible: {
			type: Boolean,
		},
		options: {
			type: Array,
		},
		filter: {
			type: Function,
			required: false,
		},
		optionValue: {
			type: String,
			default: 'id',
		},
		optionLabel: {
			type: String,
			default: 'label',
		},
		debounce: {
			type: Number,
			default: 0,
		},
	},
	data() {
		return {
			selectedOption: null,
			computedOptions: [],
		}
	},
	computed: {
		hasOptionSlot() {
			return !!this.$scopedSlots.option
		},
	},
	watch: {
		isVisible(val) {
			if (val) {
				setTimeout(() => {
					this.$refs.selectedOption.showPopup()
				}, 100)
			} else {
				this.selectedOption = null
				this.$refs.selectedOption.hidePopup()
			}
		},
	},
	methods: {
		callbackInput(e) {
			this.$emit('input', e)
		},
		hideSelect() {
			this.$emit('update:isVisible', false)
		},
	},
}
